#runExperimentButton {
  opacity: 30%;
  transition: 0.6s;
  display: flex; /* Flexbox para alinhar o contedo corretamente */
  justify-content: center; /* Centraliza o contedo horizontalmente */
  align-items: center; /* Centraliza o contedo verticalmente */
  min-height: 3rem; /* Altura mnima para garantir espao para o cone de loading */
}

#runExperimentButton:hover {
  opacity: 100%;
}

#runExperimentButton[disabled] {
  pointer-events: none; /* Desativa eventos do boto enquanto carregando */
}

#runExperimentButton .loading-icon {
  margin-left: 10px; /* Espao entre o texto e o cone de loading */
}

/* Remove o marcador padro */
li::marker {
  content: ""; /* Remove o contedo */
  display: none; /* Alternativamente, esconde o marcador */
}
